import Cookies from 'js-cookie'
import * as types from './types'
import api from '@/api/api'
import Vue from 'vue'
import { router } from '@/router/index'
import App from './app'
const vue = new Vue()

const user = {
  state: {
    info: {}
  },
  mutations: {
    [types.LOGIN]: (state, userInfo) => {
      // localStorage.clear()
      Cookies.set('user', userInfo.user.userName)
      Cookies.set('token', userInfo.accessToken)
      Cookies.set('operatorGroup', userInfo.operatorGroup ? userInfo.operatorGroup : '0')
      Cookies.set('operatorId', userInfo.operatorId ? userInfo.operatorId : '0')
      state.info = userInfo
    },
    [types.LOGOUT]: (state, vm) => {
      // api.requestLogout().then(
      //   res => {
      //     // vm.$Message.success(vue.$t('logOutSuccess'))//'注销成功'
      //     Cookies.remove('token')
      //     Cookies.remove('user')
      //     Cookies.remove('operatorGroup')
      //     Cookies.remove('operatorId')
      //     router.push({
      //       name: 'login'
      //     })
      //     // setTimeout(() => {
      //     //   window.location.reload()
      //     // }, 1000)
      //   },
      //   err => {
      //     vue.$Message.error(vue.$t('logOutFailed') + err)
      //   })
      vue.$bus.$emit('logOutStatus')
      Cookies.remove('token')
      Cookies.remove('user')
      Cookies.remove('operatorGroup')
      Cookies.remove('operatorId')
      //关闭播放清空缓存数据
      sessionStorage.removeItem('initPlay')
      //关闭记录状态
      sessionStorage.removeItem('monitorState')
      //清空画面帧率
      sessionStorage.removeItem('resolution')
      router.push({
        name: 'login'
      })
      // vm.$store.state.app.tagsList.splice(10, vm.$store.state.app.tagsList.length - 1)
      App.state.tagsList.splice(10, App.state.tagsList.length - 1)
      // Cookies.remove('user')
      Cookies.remove('password')
      Cookies.remove('access')
      // Cookies.remove('token')
      Cookies.remove('operatorGroup')
      Cookies.remove('operatorId')
      // 恢复默认样式
      let themeLink = document.querySelector('link[name="theme"]')
      // themeLink.setAttribute('href', '')
      // 清空打开的页面等数据，但是保存主题数据
      let theme = ''
      if (localStorage.theme) {
        theme = localStorage.theme
      }
      // localStorage.clear()
      if (theme) {
        localStorage.theme = theme
      }
    },
    // 修改密码
    [types.modifyPassword]: (state, vm) => {
      vm.$refs['formValidate'].validate((valid) => {
        if (valid) {
          let obj = JSON.parse(localStorage.getItem('loginRes'))
          let userId = obj.user.userId
          let param = {
            userId,
            // oldPassword: vm.formValidate.oldPassword,
            // newPassword: vm.formValidate.newPassword
            oldPassword: btoa(encodeURI(vm.formValidate.oldPassword)),  // 密码转化为base64
            newPassword: btoa(encodeURI(vm.formValidate.newPassword))
          }
          api.updatePassword(param).then(res => {
            vm.$Message.success('操作员修改密码成功')
            vm.$refs.formValidate.resetFields()
            vm.modal2 = false
          }, err => {
            vm.modal2 = false
            vm.$refs.formValidate.resetFields()
          })
        } else {
          vm.$Message.error('请输入符合规则的内容')
        }
      })
    },
    setAvator (state, path) {
      localStorage.avatorImgPath = path
    }
  }
}

export default user
