<template>
  <Menu ref="sideMenu" accordion :active-name="$route.name" :open-names="openNames" :theme="menuTheme" width="auto" @on-select="changeMenu" @on-open-change="showMenu">
    <template v-for="item in menuList">
      <Submenu :name="item.name" :key="item.path">
        <template slot="title">
          <!-- <Icon :type="item.icon" :size="iconSize"/> -->
          <img :src="item.name === 'system-manage'?system:item.name==='base-manage'?recode:item.name==='operation-manage'?operation:''" alt="" style="width:16px;vertical-align:middle;margin-right:8px">
          <span class="layout-text">{{ itemTitle(item) }}</span>
        </template>
        <template v-for="child in item.children">
          <MenuItem v-if="!child.children || child.children.length==0" :name="child.name" :key="child.name">
          <Icon :type="child.icon" :size="iconSize" :key="'icon-'+child.name"/>
          <span class="layout-text" :key="'span-'+child.name">{{ child.title }}</span>
          </MenuItem>
          <Submenu v-if="child.children && child.children.length > 0" :name="child.name" :key="child.path">
            <template slot="title">
              <Icon :type="child.icon" :size="iconSize"/>
              <span class="layout-text">{{ itemTitle(child) }}</span>
            </template>
            <template v-for="cchild in child.children">
              <MenuItem :name="cchild.name" :key="cchild.name">
              <Icon :type="cchild.icon" :size="iconSize" :key="'icon-'+cchild.name"/>
              <span class="layout-text" :key="'span-'+cchild.name">{{ cchild.title }}</span>
              </MenuItem>
            </template>
          </Submenu>
        </template>
      </Submenu>
    </template>
  </Menu>
</template>

<script>
import Cookies from 'js-cookie'
import system from '@/images/sestem.png'
import recode from '@/images/recode.png'
import operation from '@/images/operation.png'
export default {
  name: 'SidebarMenu',
  props: {
    menuList: Array,
    iconSize: Number,
    menuTheme: {
      type: String,
      default: 'dark'
    },
    openNames: {
      type: Array
    }
  },
  data(){
    return{
      system:system,
      recode:recode,
      operation:operation,
      menuName:localStorage.getItem('str'),//记录当前点击的菜单防止刷新页面重复点击
      arr : [],//二级菜单数组
      obj:this.$store.state.app.pageOpenedList,
      active:'',
      /*
       一级菜单包含项
      */
      bigMune:{
              //  "system-manage":['操作员管理','角色管理','菜单管理','权限管理'],
              //  "base-manage":['播放流元数据管理','海报分辨率管理','终端平台管理','终端信息管理','码率平台管理','地区管理','角标管理'],
              //  "commodity-manage":['产品管理','定价策略管理','个人订购','集团订购'],
              //  "customer-manage":['客户资料管理','用户组管理','用户终端解绑'],
              //  "live-manage":['直播分类管理','频道管理','频道投放','EPG管理','频道打包产品','频道排序'],
              //  "vod-manage":['媒资库','节目包管理','栏目管理',"转码模板设置","后台参数设置","转码任务查询"],
              //   "recommend-manage": ["首页推荐排序","首页推荐"]
              },
       /*
       二级菜单包含项
      */
      smallMune:{
        // 'root1':['终端平台管理','终端信息管理'],
        // 'root2':['个人订购','集团统一订购'],
        // 'root3':['媒资库','节目包管理','媒资回收站'],
        // 'root4':['转码模板测试','转码任务查询','后台参数设置']
      },
      openedLength:[],
      item:0,
    }
  },
  created(){
    let path //一级菜单路径
    let arr //一级菜单对应的标签名
    let path1 //二级菜单的路径
    let arr1 //二级菜单对应的标签名
      //取值
    for(let item of this.menuList){
      arr = []
      //对象的键
      path =  item.path
      path = path.slice(1)
      //对象的值
      var child = item.children
      //遍历二级菜单
      for(let che of child){
        //二级菜单装入
        if(!arr.includes(che.title)){
          arr.push(che.title)
        if(che.children){
            arr1 = []
            path1 = che.path.includes('/')?che.path.slice(1):che.path //如果含有三级菜单名获取他的二级菜单的路径
            this.arr.push(path1) //添加动态的二级路径
            //便历三级菜单
          for(var che1 of che.children){
              arr1.push(che1.title)
              //三级菜单装入
            if(!arr.includes(che1.title)){
              arr.push(che1.title)
            }
          }
          this.smallMune[path1] = arr1
        }
        }
      }
      this.bigMune[path] = arr
    }
  },
  mounted(){
  //菜单中的.ivu-menu-item类中的z-index属性会对第三级菜单最后一项有影响所以去掉该属性添加具有相同样式的类（不含z-index）
    // var that = this
    //  this.$(".ivu-shrinkable-menu .ivu-menu-item").each(function(index,el){
    //    that.$(this).addClass('ivu-menu-item1')
    //  })
    //  this.$(".ivu-shrinkable-menu .ivu-menu-item").each(function(index,el){
    //    that.$(this).removeClass('ivu-menu-item')
    //  })
  },
  methods: {
    //展开菜单时对菜单在进行一次处理
    showMenu(arr){
      // if(arr.length){
      //   var that = this
      //   this.$(".ivu-shrinkable-menu .ivu-menu-item").each(function(index,el){
      //     that.$(this).addClass('ivu-menu-item1')
      //   })
      //   this.$(".ivu-shrinkable-menu .ivu-menu-item").each(function(index,el){
      //     that.$(this).removeClass('ivu-menu-item')
      //   })
      // }
    },
    //判断页面是否打开
    isOpened (name) {
      var token =  Cookies.get('token')
      if(token){
        let des = JSON.parse(localStorage.getItem('pageOpenedList'))
        let des_name = []
        for (let i = 0; i < des.length; i++) {
          des_name.push(des[i].name)
        }
        return des_name.indexOf(name) > -1
      }
    },
    changeMenu (active) {
      //console.log(this.bigMune)
      //添加一个公共数据监测用户点击的菜单
       this.active = active
      //先判断如果该标签页是关闭状态时
      if(!this.isOpened(active)){
         this.menuName =''
      }
      if(this.menuName != active){
         //this.openNames.pop()
         this.menuName = active
         var num = 0
         /*
          发现个问题当点击触发三级菜单时前面所有的二级菜单都会是打开状态所
          所以思路是将找到openNames数组中包含的二级最大索引，将前面的二级菜单全部切掉
         */
         for(var item of this.arr){
            if(this.openNames.indexOf(item) >= num){
              num = this.openNames.indexOf(item)
            }
         }
         if(num==0){
            //this.openNames.splice(0,1)
         }else{
            //this.openNames.splice(0,num)
         }
         localStorage.str = active
         this.$emit('on-change', active)
      }else{
        //激活标签页（详见shrinkable-menu.vue组件）
        this.$emit('on-change', active)
        //禁止两次重复点击同一菜单
        return false
      }
      //this.oldOpenNames=this.openNames.slice(0)
      // if(this.oldOpenNames.length==0){
      //   if(active=='操作员管理'||'角色管理'||'菜单管理'||"权限管理"){
      //     this.first = 'system'
      //   }else if(active=='播放流元数据管理'||'海报分辨率管理'||'终端管理'||"地区管理"||'角标管理'){
      //     this.first ="base-manage"
      //   }else if(active=='产品管理'||'定价策略管理'||'订购管理'){
      //      this.first ="commodity-manage"
      //   }
      // }
      // console.log('old',this.oldOpenNames,this.oldOpenNames.length)
      // console.log('new',this.openNames,this.openNames.length)
      // // if(this.oldOpenNames.length>1){
      // // }
      // if(this.muneName !== active){
      //    this.muneName = active
      //    this.first = this.oldOpenNames[this.oldOpenNames.length-1]
      //    console.log(this.first)
      //    console.log(this.oldOpenNames[this.openNames.length-1],this.openNames.length)
      //    console.log(this.openNames)
      //   // this.openNames.push(this.first)
      // }else{
      //    if(!this.openNames.includes(this.first)&&this.openNames.length<=1){
      //       this.openNames.push(this.first)
      //    }
      //   console.log(this.openNames)
      // }
      //this.$emit('on-change', active)
      //console.log(this.openNames)
    },
    itemTitle (item) {
      console.log('哈哈',item)
      if (typeof item.title === 'object') {
        return this.$t(item.title.i18n)
      } else {
        return item.title
      }
    }
  },
  //监听标签页的变化
  watch: {
    obj: {
      handler(){
        //console.log('list',this.menuList)
         //console.log(this.openNames)
         //console.log('big',this.bigMune)
         //console.log('small',this.smallMune)
        //console.log('二级',this.arr)
        //将打开标签页的长度放到数组内
        this.openedLength.push(this.obj.length)
        //删除标签页的情况最后一项小于倒数第二项
        if(this.openedLength[this.openedLength.length-1] < this.openedLength[this.openedLength.length-2]){
          //将打开页码的目录与一级菜单的目录项进行匹配(一級菜單驗證)
          for(var key in this.bigMune){
            var flag = true
            for(var item of this.obj){
              var title = item.title
              if(this.bigMune[key].includes(title)){
                this.item = key
                flag = false
              }else{
                this.item = key
              }
            }
            //当打开页不在菜单时
            if(flag){
              //openNames中删除该项
              var index = this.openNames.indexOf(this.item)
              if(index > -1){
                // this.openNames.splice(index,1)
              }
            }
         }
         //将打开页码的目录与二级菜单的目录项进行匹配(二级級菜單驗證)
          for(var key in this.smallMune){
            var flag = true
            for(var item of this.obj){
              var title = item.title
              if(this.smallMune[key].includes(title)){
                this.item = key
                flag = false
              }else{
                this.item = key
              }
            }
            //当打开页不在菜单时
            if(flag){
              //openNames中删除该项
              var index = this.openNames.indexOf(this.item)
              if(index > -1){
                // this.openNames.splice(index,1)
              }
            }
         }
       }
     },
      // 页面加载之初先执行一次handle
      //immediate: true,
      // 深度检查属性,即使对象内部的属性值改变, 也能检测到(比较消耗性能)
      deep: true
    }
  },
  updated () {
    this.$nextTick(() => {
      if (this.$refs.sideMenu) {
        this.$refs.sideMenu.updateOpened()
      }
    })
  }
}
</script>