<style lang="less">
@import './main.less';
</style>
<template>
  <div class="main" :class="{'main-hide-text': shrink}">
    <div class="sidebar-menu-con" :style="{width: shrink?'60px':'160px', overflow: 'hidden'}" style="background:rgba(7,17,66,1);">
      <shrinkable-menu
        :shrink="shrink"
        @on-change="handleSubmenuChange"
        :theme="menuTheme"
        :before-push="beforePush"
        :open-names="openedSubmenuArr"
        :menu-list="menuList">
        <!-- <div slot="top" class="logo_con"> -->
        <div slot="top" style="height:60px;line-height:60px;">
          <img src="../images/logo.png" style="width:30px;vertical-align:middle;margin-left:10px;"/>
          <!-- <span style="font-size:15px;font-weight:600;color:rgba(255,255,255,1);margin-left:6px;">数字录播系统</span> -->
          <span style="font-size:15px;font-weight:600;color:rgba(255,255,255,1);margin-left:6px;">{{$t('main.systemName')}}</span>
          <!--  -->
          <!-- <img v-show="shrink" src="../images/logo.png" key="min-logo" > -->
          <!-- <img v-show="!shrink" src="../images/logo.png" key="max-logo" > -->
          <!-- <img v-show="shrink" src="../images/logo-min.png" key="min-logo" /> -->
        </div>
      </shrinkable-menu>
    </div>
    <div class="main-header-con" :style="{paddingLeft: shrink?'60px':'140px'}">
      <div class="main-header" style="background:rgba(7,17,66,1);">
        <!-- 菜单切换按钮 参照效果图取消 -->
        <!-- <div class="navicon-con">
                    <Button :style="{transform: 'rotateZ(' + (this.shrink ? '-90' : '0') + 'deg)'}" type="text" @click="toggleClick">
                        <Icon type="navicon" size="32"></Icon>
                    </Button>
                </div> -->
        <div class="header-middle-con" style="left: 0; padding: 0">
          <!-- <div class="main-breadcrumb"> -->
          <div>
            <!-- <breadcrumb-nav :currentPath="currentPath"></breadcrumb-nav> -->
            <navigation-menu/>
          </div>
        </div>
        <div class="header-avator-con">
          <!-- 根据效果图将四个组件取消 -->
          <!-- <full-screen v-model="isFullScreen" @on-change="fullscreenChange"></full-screen>
                    <lock-screen></lock-screen>
                    <message-tip v-model="mesCount"></message-tip>
                    <theme-switch></theme-switch> -->

          <div class="user-dropdown-menu-con">
            <Row type="flex" justify="end" align="middle" class="user-dropdown-innercon">
              <Dropdown transfer trigger="click" @on-click="handleClickUserDropdown">
                <a href="javascript:void(0)" style="color: white">
                  <!-- <Icon type="md-power" style="margin-right:6px;"/> -->
                  <span class="main-user-name"><Icon type="md-power" style="margin-right:6px;"/>{{ userName }}</span>
                  <Icon type="ios-arrow-down" style="margin-left:10px;margin-top:36px;" />
                  <Icon type="arrow-down-b"/>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem id="passwordModify" name="passwordModify">{{$t('main.passwordModify')}}</DropdownItem>
                  <DropdownItem id="loginout" name="loginout">{{$t('main.exit')}}</DropdownItem>
                </DropdownMenu>
              </Dropdown>
              <!-- <Avatar :src="avatorPath" style="background: #619fe7;margin-left:10px;"/> -->
            </Row>
          </div>
        </div>
      </div>
    </div>
    <div class="single-page-con" :style="{left: shrink?'60px':'160px'}">
      <div class="single-page" style="height:98%">
        <keep-alive :include="cachePage">
          <router-view/>
        </keep-alive>

        <!--密码修改-->
        <Modal class-name="vertical-center-modal"  v-model="modal2" :mask-closable="false" ok-text="确定" cancel-text="取消" width="655" @on-cancel="changePasswordShut">
          <p slot="header" style="color:#484f5f;text-align:left">
            <Icon type="search" />
            <span>{{$t('main.passwordModify')}}</span>
          </p>
          <Form  ref="formValidate" :model="formValidate" :rules="ruleValidate" label-position="right" :label-width="140" style="margin-top:35px;">
            <Row type="flex" justify="center" align="middle" :gutter="16" style="">
              <Col span="22">
              <FormItem :label="$t('main.oldPassword')"  prop="oldPassword">
                <Input type="password" v-model="formValidate.oldPassword" style="width:351px;height:32px;"></Input>
                <!-- <span style="color:rgba(175,188,204,1);margin-left:10px;">{{ oldPasswordLength }}/20</span> -->
              </FormItem>
              </Col>
            </Row>
            <Row type="flex" justify="center" align="middle" :gutter="16" style="">
              <Col span="22">
              <FormItem :label="$t('main.newPassword')"  prop="newPassword" >
                <Input type="password" v-model="formValidate.newPassword" style="width:351px;height:32px;"></Input>
                <!-- <span style="color:rgba(175,188,204,1);margin-left:10px;">{{ newPasswordLength }}/20</span> -->
              </FormItem>
              </Col>
            </Row>
            <Row type="flex" justify="center" align="middle" :gutter="16">
              <Col span="22">
              <FormItem :label="$t('main.newPasswordConfirm')" prop="newPasswordConfirm" >
                <Input type="password" v-model="formValidate.newPasswordConfirm" style="width:351px;height:32px;"></Input>
                <!-- <span style="color:rgba(175,188,204,1);margin-left:10px;">{{ newPasswordConfirmLength }}/20</span> -->
              </FormItem>
              </Col>
            </Row>
          </Form>
          <div slot="footer" style="text-align:center;margin-bottom:20px">
            <Button @click="changePasswordShut" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">{{$t('main.cancel')}}</Button>
            <Button type="primary" @click="passwordModifySubmit('formValidate')" style="margin-left:49px;">{{$t('main.ok')}}</Button>
          </div>
        </Modal>

        <!-- 退出提示框 -->
        <Modal v-model="modal3" :mask-closable="false" :closable="false" :styles="{top: '20px'}"> 
          <!-- <div>提示</div> -->
          <p slot="header" style="text-align:left;padding-left:5px;">
            {{$t('main.info')}}
          </p>
          <span style="color:rgba(255,255,255,1);">{{$t('main.exitInfo')}}</span>
          <p slot="footer" style="display:flex;justify-content:center">
            <Button @click="exit" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">{{$t('main.insistExit')}}</Button>
            <Button @click="toExport" type="primary" :disabled="isGray" style="margin-left:49px">{{$t('main.toExport')}}</Button>
          </p>
        </Modal>
      </div>
    </div>
  </div>
</template>
<script>
import shrinkableMenu from './main-components/shrinkable-menu/shrinkable-menu.vue'
import tagsPageOpened from './main-components/tags-page-opened.vue'
// import breadcrumbNav from './main-components/breadcrumb-nav.vue';
import navigationMenu from './main-components/navigation-menu.vue'
// import fullScreen from './main-components/fullscreen.vue';
// import lockScreen from './main-components/lockscreen/lockscreen.vue';
// import messageTip from './main-components/message-tip.vue';
// import themeSwitch from './main-components/theme-switch/theme-switch.vue';
import Cookies from 'js-cookie'
import util from '@/libs/util.js'
import avatar from '../images/avatar-default.jpg'
import Main from '@/views/Main.vue'
import VoidSinglePage from '@/views/main-components/void-single-page.vue'
import api from '@/api/api'

import MenuUtils from '@/libs/MenuUtils'

export default {
  components: {
    shrinkableMenu,
    // tagsPageOpened,
    // breadcrumbNav,
    navigationMenu
    // fullScreen,
    // lockScreen,
    // messageTip,
    // themeSwitch
  },
  data () {
    const validatePassword = (rule, value, callback) => {
     if (
        this.formValidate.newPassword !==
        this.formValidate.newPasswordConfirm
      ) {
        return callback(new Error(this.$t('main.passwordInconsistent')))
      } else {
        callback()
      }
    }

    return {
      shrink: false,
      userName: '',
      isFullScreen: false,
      openedSubmenuArr: this.$store.state.app.openedSubmenuArr,
      modal2:false,
      modal3:false,
      isGray:false, // 操作员登录时拿到的operationPermissions的值，默认为false;
      hasChannelManage:false, // 用户是否拥有“通道管理”菜单权限，默认为false，没有该菜单权限
      formValidate:{
         oldPassword:'',
         newPassword:'',
         newPasswordConfirm:''
      },
      ruleValidate: {
        oldPassword: [
          {
            required: true,
            message: this.$t('main.enterOriginalPassword'),
            trigger: 'blur'
          },
          {
            min: 4,
            message: this.$t('main.minCharLength4'),
            trigger: 'blur'
          },
          { max: 20, message: this.$t('main.maxCharLength20'), trigger: 'blur' }
        ],
        newPassword: [
          {
            required: true,
            message: this.$t('main.enterNewPassword'),
            trigger: 'blur'
          },
          // {
          //   pattern: /^\S+$/,
          //   message: '请输入长度4 - 20 位英文、 数字、 特殊字符',
          //   trigger: 'blur'
          // },
          {
            pattern: /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$&*]+$)[a-zA-Z\d!@#$&*]+$/,
            message: this.$t('main.enterCharReg4To20'),
            trigger: 'blur'
          },
          {
            min: 4,
            message: this.$t('main.minCharLength4'),
            trigger: 'blur'
          },
          { max: 20, message: this.$t('main.maxCharLength20'), trigger: 'blur' }
        ],
        newPasswordConfirm: [
          { required: true, trigger: 'blur', message: this.$t('main.enterNewPasswordAgain') },
          { max: 20, message: this.$t('main.maxCharLength20'), trigger: 'blur' },
          {validator: validatePassword}
        ],
      }
    }
  },
  computed: {
    menuList () {
      let appRouter = this.$store.state.app.menuList
      let menuArray = JSON.parse(localStorage.getItem('menuDataList'))
      let menuData0 = JSON.parse(localStorage.getItem('loginRes'))
      let menuData = menuData0.user.permissions
      // TODO 动态
      let data = JSON.parse(localStorage.getItem('loginRes'))
      let tempArr = []
      MenuUtils(tempArr, data.user.permissions)
      var id2index = []
      for (let i = 0; i < tempArr.length; i++) {
        id2index[tempArr[i].id] = i
      }
      var finalRouter = []
      for (let item of tempArr) {
      // console.log(item)
        if (item) {
          if (item.parentId !== '0') {
            // console.log(initROUTER[item.name])
            // 通过item的parentId进行children的拼装                 //tempArr[id2index[item.parentId]].hasOwnProperty('children')
            if (tempArr[id2index[item.parentId]] !== undefined && !Object.prototype.hasOwnProperty.call(tempArr[id2index[item.parentId]],'children')) {
              // item.children = []
              tempArr[id2index[item.parentId]].children = []
            }
            if (tempArr[id2index[item.id]].id && tempArr[id2index[item.parentId]] !== undefined) {
              tempArr[id2index[item.parentId]].children.push(tempArr[id2index[item.id]])
            }
          } else {
            finalRouter.push(tempArr[id2index[item.id]])
          }
        }
      }
      return finalRouter
    },
    currentPath () {
      return this.$store.state.app.currentPath // 当前面包屑数组
    },
    avatorPath () {
      return localStorage.avatorImgPath || avatar
    },
    cachePage () {
      return this.$store.state.app.cachePage
    },
    lang () {
      return this.$store.state.app.lang
    },
    menuTheme () {
      return this.$store.state.app.menuTheme
    },
    mesCount () {
      return this.$store.state.app.messageCount
    }
  },
  watch: {
    '$route' (to) {
      console.log('to',to.name)
      this.$store.commit('setCurrentPageName', to.name)
      let pathArr = util.setCurrentPath(this, to.name)
      if (pathArr.length > 3) {
         this.$store.commit('addOpenSubmenu',pathArr)
      } else if (pathArr.length > 2) {
        this.$store.commit('addOpenSubmenu', pathArr[1].name)
      }
      localStorage.currentPageName = to.name
    },
    lang () {
      util.setCurrentPath(this, this.$route.name) // 在切换语言时用于刷新面包屑
    }
  },
  mounted () {
    this.init()
  },
  created () {
    this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true;
    // this.hasChannelManage = localStorage.getItem('hasChannelManage');
    // console.log("3333333", localStorage.getItem('hasChannelManage'))
    // if (this.isGray == false && this.hasChannelManage == false) {
    //   this.isGray = true;
    // }
    // if (this.isGray == true && )
    //-------------------修复左侧菜单刷新后收起问题
    //获取当前激活路由名称
    let name = this.$store.state.app.currentPageName
    let pathArr = util.setCurrentPath(this,name)
    if (pathArr.length > 3) {
        this.$store.commit('addOpenSubmenu',pathArr)
    } else if (pathArr.length > 2) {
      this.$store.commit('addOpenSubmenu', pathArr[1].name)
    }
    //-----------------------------------------------
    // 显示打开的页面的列表
    this.$store.commit('setOpenedList')
  },
  methods: {
    //密码修改提交
    passwordModifySubmit(name){
      // this.$store.commit('modifyPassword',this);
      this.$refs['formValidate'].validate((valid) => {
        if (valid) {
          let obj = JSON.parse(localStorage.getItem('loginRes'))
          let userId = obj.user.userId
          let param = {
            userId,
            oldPassword: btoa(encodeURI(this.formValidate.oldPassword)),  // 密码转化为base64
            newPassword: btoa(encodeURI(this.formValidate.newPassword))
          }
          api.updatePassword(param).then(res => {
            this.$Message.success(this.$t('main.modifyPasswordSuccess'))
            this.modal2 = false
            console.log('lll',this.$sumaQtPlayer)
            /**显示播放器 */
            let param = [{
              groupIndex:-2,
              playerIndex:-2,
              channelId:'',
              business:{},
              player:{
                isVisible:true
              }
            }]
            this.$bus.$emit('showPlay',param)
            this.$refs.formValidate.resetFields()
          })
        } else {
          this.$Message.error(this.$t('main.enterRuleContent'))
        }
      })
    },

    //关闭修改密码弹窗
    changePasswordShut(){
      this.$refs.formValidate.resetFields()
      this.modal2 = false
      console.log('lll',this.$sumaQtPlayer)
      /**显示播放器 */
       let param = [{
          groupIndex:-2,
          playerIndex:-2,
          channelId:'',
          business:{},
          player:{
            isVisible:true
          }
        }]
        this.$bus.$emit('showPlay',param)
    },

    init () {
      let pathArr = util.setCurrentPath(this, this.$route.name)
      this.$store.commit('updateMenulist')
      if (pathArr.length >= 2) {
        this.$store.commit('addOpenSubmenu', pathArr[1].name)
      }
      this.userName = Cookies.get('user')
      let messageCount = 3
      this.messageCount = messageCount.toString()
      this.$store.commit('setMessageCount', 3)
      this.$Message.config({duration: 5})
    },
    toggleClick () {
      this.shrink = !this.shrink
    },
    handleClickUserDropdown (name) {
      if (name === 'ownSpace') {
    
      } else if (name === 'loginout') {
        if(sessionStorage.getItem('hasChnanel')=='true'){
           this.modal3 = true;
        }else{
           this.$store.commit('logout', this)
           this.$store.commit('clearOpenedSubmenu')
           localStorage.pageOpenedList = []
        }
        // 退出登录
        // this.$store.commit('clearOpenedSubmenu')
        // 清空已经打开的tab页信息
      } else if(name === 'passwordModify'){
        this.modal2 = true
        console.log('ttt',this.$sumaQtPlayer)
        /**隐藏播放器 */
        let param = [{
          groupIndex:-2,
          playerIndex:-2,
          channelId:'',
          business:{},
          player:{
            isVisible:false
          }
        }]
        this.$bus.$emit('showPlay',param)
      }
    },

    /** 坚持退出 */
    exit(){
      this.$store.commit('logout', this)
      this.$store.commit('clearOpenedSubmenu')
      localStorage.pageOpenedList = []
    },

    /** 去导出 */
    toExport(){
      this.modal3 = false;
      this.$router.push({
        name:'channelMonitor'
      })
    },

    handleSubmenuChange (val) {
    },
    beforePush (name) {
      return true
    },
    fullscreenChange (isFullScreen) {
    }
  }
}
</script>
<style>
.ivu-dropdown-item{
  color:rgba(254,254,254,1)!important;
}
.ivu-dropdown-item:hover{
  background:rgba(41,100,148,1)!important;
}
</style>
