<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
 import api from '@/api/api'
export default {
  data(){
    return {
      websocketUrl:'',
      ws:null
    }
  },
  methods:{
   getWebsocket() {
      let param = {};
      api.getPublicConfig(param).then(res => {
        console.log("问地址", res.result.websocketAddress);
        this.websocketUrl = res.result.websocketAddress
        this.websocket(this.websocketUrl);
        sessionStorage.setItem('publicData',JSON.stringify(res.result))
      });
    },
    websocket(url) {
      let vm = this;
      this.ws = new WebSocket(url);
      console.log('ws1', this.ws)
      this.ws.onopen = function() {
        console.log("client：打开连接");
        if(sessionStorage.getItem('login_status')=== 'in'){
          let param = {
            type:1,
            userName:JSON.parse(localStorage.getItem('loginRes')).user.userName,
            userId:JSON.parse(localStorage.getItem('loginRes')).user.userId
          }
          vm.ws.send(JSON.stringify(param));
          setTimeout(() => {
             vm.$bus.$emit("handlerChange")
          }, 200);
        }
      };
      this.ws.onmessage = function(e) {
        let data = JSON.parse(e.data);
        let date = m.getDate(data.sendDate);
      };
      this.ws.onclose = function(params) {
        console.log("client：关闭连接");
      };
    },
  },
  created(){
    this.$bus.$off('loginStatus')
    this.$bus.$off('logOutStatus')
    this.$bus.$on('loginStatus',(val)=>{
      console.log('接收到登录状态',val)
      console.log('ws',this.ws)
      if(this.ws && this.ws.readyState === 1){
        let param = {
          type:1,
          userName:val.user.userName,
          userId:val.user.userId
        }
        sessionStorage.setItem('login_status','in')
        this.ws.send(JSON.stringify(param));
      }
    })
    this.$bus.$on('logOutStatus',val=>{
      if(this.ws && this.ws.readyState === 1){
       let param = {
          type:2,
          userName:JSON.parse(localStorage.getItem('loginRes')).user.userName,
          userId:JSON.parse(localStorage.getItem('loginRes')).user.userId
        }
        this.ws.send(JSON.stringify(param));
        sessionStorage.setItem('login_status','out')
      }
    })
    this.getWebsocket()
  }
}
</script>
<style>
#app {
  width: 100%;
  height: 100%;
  margin:0 auto
}
