import {otherRouter, appRouter} from '@/router/router'
import Util from '@/libs/util'
import Cookies from 'js-cookie'
import Vue from 'vue'
import * as types from './types'
const app = {
  state: {
    cachePage: [],
    lang: '',
    isFullScreen: false,
    // 频道管理
    pageSize: 10,
    pageNo: 1,
    // 频道投放
    pageSize1: 10,
    pageNo1: 1,
    openedSubmenuArr: [], // 要展开的菜单数组
    menuTheme: 'dark', // 主题
    themeColor: '',
    pageOpenedList: [{
      title: '首页',
      path: '',
      name: 'home_index'
    }],
    currentPageName: '',
    currentPath: [
      {
        title: '首页',
        path: '',
        name: 'home_index'
      }
    ], // 面包屑数组
    menuList: [],
    routers: [
      otherRouter,
      ...appRouter
    ],
    tagsList: [...otherRouter.children],
    messageCount: 0,
    dontCache: ['text-editor', 'artical-publish'], // 在这里定义你不想要缓存的页面的name属性值(参见路由配置router.js)
    clientItem: [],
    // tabCount表示允许打开标签数量的最大值
    tabCount: 9,
    // 拖拽树点击type
    clickType: 0,
    // 拖拽树全选/反选
    isChecked: false,
    // 拖拽树复选框选择项目
    treeSelectedItems: [],
    // 频道投放标签关闭的flag
    tagCloseFlag: false
  },
  mutations: {
    [types.setTagsList]: (state, list) => {
      state.tagsList.push(...list)
    },
    updateMenulist (state) {
      let accessCode = parseInt(Cookies.get('access'))
      let menuList = []
      appRouter.forEach((item, index) => {
        if (item.access !== undefined) {
          if (Util.showThisRoute(item.access, accessCode)) {
            if (item.children.length === 1) {
              menuList.push(item)
            } else {
              let len = menuList.push(item)
              let childrenArr = []
              childrenArr = item.children.filter(child => {
                if (child.access !== undefined) {
                  if (child.access === accessCode) {
                    return child
                  }
                } else {
                  return child
                }
              })
              menuList[len - 1].children = childrenArr
            }
          }
        } else {
          if (item.children.length === 1) {
            menuList.push(item)
          } else {
            let len = menuList.push(item)
            let childrenArr = []
            childrenArr = item.children.filter(child => {
              if (child.access !== undefined) {
                if (Util.showThisRoute(child.access, accessCode)) {
                  return child
                }
              } else {
                return child
              }
            })
            let handledItem = JSON.parse(JSON.stringify(menuList[len - 1]))
            handledItem.children = childrenArr
            menuList.splice(len - 1, 1, handledItem)
          }
        }
      })
      state.menuList = menuList
    },
    // 频道管理的分页记忆
    changeChannelSize (state, size) {
      state.pageSize = size
    },
    changeChannelPage (state, index) {
      state.pageNo = index
    },
    // 频道投放的分页记忆
    changeChannelSize1 (state, size) {
      state.pageSize1 = size
    },
    changeChannelPage1 (state, index) {
      state.pageNo1 = index
    },
    changeMenuTheme (state, theme) {
      state.menuTheme = theme
    },
    changeMainTheme (state, mainTheme) {
      state.themeColor = mainTheme
    },
    addOpenSubmenu (state, name) {
      let hasThisName = false
      let isEmpty = false
      if (name instanceof Array) {
        state.openedSubmenuArr.length = 0
        state.openedSubmenuArr.push(name[1].name)
        state.openedSubmenuArr.push(name[2].name)
      } else {
        if (name.length === 0) {
          isEmpty = true
        }
        if (state.openedSubmenuArr.indexOf(name) > -1) {
          hasThisName = true
        }
        if (!hasThisName && !isEmpty) {
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        }
      }
    },
    addOpenSubmenu1 (state, name) {
      let hasThisName = false
      let isEmpty = false
      if (name.length === 0) {
        isEmpty = true
      }
      if (state.openedSubmenuArr.indexOf(name) > -1) {
        hasThisName = true
      }
      if (!hasThisName && !isEmpty) {
        if (name === 'live-manage-channel-new' || name === 'details-live-manage-channel') {
          name = 'live-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        } else if (name === 'user-information') {
          name = 'customer-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        } else if (name === 'details-usergroup') {
          name = 'customer-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        } else if (name === 'details-dvb') {
          name = 'live-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        } else if (name === 'live-manage-epgDetails' || name === 'live-manage-channel-new') {
          name = 'live-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        } else if (name === 'player-flow') {
          name = 'base-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
          state.openedSubmenuArr.push('root1')
        } else if (name === 'subcollectionList' || name === 'check-trailer' || name === 'check-all') {
          name = 'vod-manage'
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
          state.openedSubmenuArr.push('root3')
        } else {
          state.openedSubmenuArr.length = 0
          state.openedSubmenuArr.push(name)
        }

        // console.log('aa', state.openedSubmenuArr)
      }
    },
    closePage (state, name) {
      state.cachePage.forEach((item, index) => {
        if (item === name) {
          state.cachePage.splice(index, 1)
        }
      })
    },
    initCachepage (state) {
      if (localStorage.cachePage) {
        state.cachePage = JSON.parse(localStorage.cachePage)
      }
    },
    removeTag (state, name) {
      state.pageOpenedList.map((item, index) => {
        if (item.name === name) {
          state.pageOpenedList.splice(index, 1)
        }
      })
    },
    pageOpenedList (state, get) {
      let openedPage = state.pageOpenedList[get.index]
      if (get.argu) {
        openedPage.argu = get.argu
      }
      if (get.query) {
        openedPage.query = get.query
      }
      state.pageOpenedList.splice(get.index, 1, openedPage)
      localStorage.pageOpenedList = JSON.stringify(state.pageOpenedList)
    },
    clearAllTags (state) {
      state.pageOpenedList.splice(1)
      state.cachePage.length = 0
      localStorage.pageOpenedList = JSON.stringify(state.pageOpenedList)
    },
    clearOtherTags (state, vm) {
      let currentName = vm.$route.name
      let currentIndex = 0
      state.pageOpenedList.forEach((item, index) => {
        if (item.name === currentName) {
          currentIndex = index
        }
      })
      if (currentIndex === 0) {
        state.pageOpenedList.splice(1)
      } else {
        state.pageOpenedList.splice(currentIndex + 1)
        state.pageOpenedList.splice(1, currentIndex - 1)
      }
      let newCachepage = state.cachePage.filter(item => {
        return item === currentName
      })
      state.cachePage = newCachepage
      localStorage.pageOpenedList = JSON.stringify(state.pageOpenedList)
    },
    setOpenedList (state) {
      state.pageOpenedList = localStorage.pageOpenedList ? JSON.parse(localStorage.pageOpenedList) : [otherRouter.children[0]]
    },
    setCurrentPath (state, pathArr) {
      state.currentPath = pathArr
    },
    setCurrentPageName (state, name) {
      state.currentPageName = name
    },
    switchLang (state, lang) {
      state.lang = lang
      Vue.config.lang = lang
    },
    clearOpenedSubmenu (state) {
      state.openedSubmenuArr.length = 0
    },
    setMessageCount (state, count) {
      state.messageCount = count
    },
    [types.increateTag]: (state, tagObj) => {
      if (!Util.oneOf(tagObj.name, state.dontCache)) {
        state.cachePage.push(tagObj.name)
        localStorage.cachePage = JSON.stringify(state.cachePage)
      }
      // console.log(state.pageOpenedList.length)
      if (state.pageOpenedList.length <= state.tabCount) {
        state.pageOpenedList.push(tagObj)
        localStorage.pageOpenedList = JSON.stringify(state.pageOpenedList)
      }
    },
    setClientItem (state, item) {
      state.clientItem = item
    },
    // 更新tagColseFlag
    updateTagCloseFlagToTrue (state) {
      state.tagCloseFlag = true
    },
    updateTagCloseFlagToFalse (state) {
      state.tagCloseFlag = false
    }
  }
}

export default app
