import Vue from 'vue'
import VueI18n from 'vue-i18n'
import localeZh from './zh_CN'
import localeEn from './en_US'
import zhLocale from 'view-design/dist/locale/zh-CN'
import enLocale from 'view-design/dist/locale/en-US'

Vue.use(VueI18n)

const messages = {
  zh: {
    ...localeZh,
    ...zhLocale
  },
  en: {
    ...localeEn,
    ...enLocale
  }
}

// 获取当前浏览器语言
// const lang = navigator.language || navigator.userLanguage;

// const i18n = new VueI18n({
//   locale: lang.substr(0, 2),
//   messages
// })

const lang = window.navigator.systemLanguage;
const i18n = new VueI18n({
  locale: lang,
  messages
})


export default i18n
