var toadd = []
var errorPage = [{
  path: '/*',
  name: 'error-404',
  meta: {
    title: '404-页面不存在'
  },
  component: () =>
    import('@/views/error-page/404.vue')
}]

// 手动刷新404问题
let data = JSON.parse(localStorage.getItem('loginRes'))
if (data) {
  // console.log('刷新', data.user.permissions)
  let tempArr = []
  MenuUtils(tempArr, data.user.permissions)
  var id2index = []
  for (let i = 0; i < tempArr.length; i++) {
    id2index[tempArr[i].id] = i
  }
  var finalRouter = []
  for (let item of tempArr) {
    // console.log(item)
    if (item) {
      if (item.parentId !== '0') {
        // console.log(initROUTER[item.name])
        // 通过item的parentId进行children的拼装
        if (tempArr[id2index[item.parentId]] !== undefined && !Object.prototype.hasOwnProperty.call(tempArr[id2index[item.parentId]],'children')) {
          // item.children = []
          tempArr[id2index[item.parentId]].children = []
        }
        if (tempArr[id2index[item.id]].id && tempArr[id2index[item.parentId]] !== undefined) {
          tempArr[id2index[item.parentId]].children.push(tempArr[id2index[item.id]])
        }
      } else {
        finalRouter.push(tempArr[id2index[item.id]])
      }
    }
  }
  // console.log(toadd, finalRouter)
  router.addRoutes(finalRouter)
  router.addRoutes(errorPage)
}

router.beforeEach((to, from, next) => {
  // console.log('跳转。。。', router, JSON.parse(localStorage.getItem('loginRes')))
  if (JSON.parse(localStorage.getItem('loginRes')) !== null) {
    let data = JSON.parse(localStorage.getItem('loginRes'))
    if (data) {
      // console.log('刷新', data.user.permissions)
      let tempArr = []
      MenuUtils(tempArr, data.user.permissions)
      var id2index = []
      for (let i = 0; i < tempArr.length; i++) {
        id2index[tempArr[i].id] = i
      }
      var finalRouter = []
      for (let item of tempArr) {
        // console.log(item)
        if (item) {
          if (item.parentId !== '0') {
            // console.log(initROUTER[item.name])
            // 通过item的parentId进行children的拼装                 //!tempArr[id2index[item.parentId]].hasOwnProperty('children')
            if (tempArr[id2index[item.parentId]] !== undefined && !Object.prototype.hasOwnProperty.call(tempArr[id2index[item.parentId]],'children')) {
              // item.children = []
              tempArr[id2index[item.parentId]].children = []
            }
            if (tempArr[id2index[item.id]].id && tempArr[id2index[item.parentId]] !== undefined) {
              tempArr[id2index[item.parentId]].children.push(tempArr[id2index[item.id]])
            }
          } else {
            finalRouter.push(tempArr[id2index[item.id]])
          }
        }
      }
    }
    toadd = finalRouter
  } else {
    toadd = []
    // 清空上一次的Cookies信息
    Cookies.remove('user')
    Cookies.remove('token')
    Cookies.remove('operatorId')
    Cookies.remove('operatorGroup')
  }
  iView.LoadingBar.start()
  Util.title(to.meta.title)
  if (!Cookies.get('user') && to.name !== 'login') {
    // 判断是否已经登录且前往的页面不是登录页
    next({
      name: 'login'
    })
  } else if (Cookies.get('user') && to.name === 'login') {
    // 判断是否已经登录且前往的是登录页
    Util.title()
    next({
      // name: sessionStorage.getItem('firstPage')
      path: localStorage.getItem('firstPage')
    })
  } else {
    const curRouterObj = Util.getRouterObjByName([otherRouter, ...toadd], to.name)
    if (curRouterObj && curRouterObj.path !== 'home') {
      // 需要判断权限的路由
      let menuArray = JSON.parse(localStorage.getItem('menuDataList'))
      if (menuArray.indexOf(curRouterObj.title) > -1) {
        Util.toDefaultPage([otherRouter, ...toadd], to.name, router, next) // 如果在地址栏输入的是一级菜单则默认打开其第一个二级菜单的页面
      } else {
        // debugger;
        next({
          replace: true,
          name: 'error-403'
        })
      }
    } else {
      let menuArray1 = JSON.parse(localStorage.getItem('menuDataList'))
      if (menuArray1 && menuArray1.indexOf(to.name) === -1 && to.name !== 'home' && to.name !== 'home_index' && to.name !== 'login' && to.name.split('-')[0] !== 'error') {
        if (!curRouterObj) {
          // debugger;
          next({
            replace: true,
            name: 'error-403'
          })
        } else {
          next({
            replace: true,
            name: to.name
          })
        }
      } else {
        // 没有配置权限的路由, 直接通过
        Util.toDefaultPage([...routers], to.name, router, next)
      }
    }
  }
})

router.afterEach((to) => {
  Util.openNewPage(router.app, to.name, to.params, to.query)
  iView.LoadingBar.finish()
  window.scrollTo(0, 0)
})



new Vue({
  el: '#app',
  i18n,
  router: router,
  store: store,
  data: {
    currentPageName: ''
  },
  mounted () {
    this.currentPageName = this.$route.name
    // 显示打开的页面的列表
    this.$store.commit('setOpenedList')
    this.$store.commit('initCachepage')
    // 权限菜单过滤相关
    this.$store.commit('updateMenulist')
    // iview-admin检查更新
    // util.checkUpdate(this);
  },
  created () {
    let tagsList = []
    toadd.map((item) => {
      if (item.children.length <= 1) {
        tagsList.push(item.children[0])
      } else {
        tagsList.push(...item.children)
      }
    })
    this.$store.commit('setTagsList', tagsList)
  },
  render: (h) => h(App)
})

