/**
 * http配置
 */
import axios from 'axios'
import store from '../store/index'
import appConfig from '../../build/config.js'
import Cookies from 'js-cookie'
import { Message } from 'view-design'
import { router } from '@/router/index'
import * as types from '../store/modules/types'

// axios 配置
axios.defaults.timeout = 5000
// axios.defaults.baseURL = appConfig.baseURL
axios.defaults.baseURL = '/'
// 带cookie请求
axios.defaults.withCredentials = true
// 设置默认请求头
axios.defaults.headers.post['Content-Type'] = 'application/json'

// http request 拦截器
axios.interceptors.request.use(
  config => {
    let token = Cookies.get('token')
    let operatorGroup = Cookies.get('operatorGroup')
    let operatorId = Cookies.get('operatorId')
    if (token) {
      config.headers.accessToken = `${token}`
    }
    // 统一添加请求公共信息
    const info = config.data
    config.data = {
      versionName: appConfig.versionName,
      versionCode: appConfig.versionCode,
      operatorGroup: operatorGroup,
      operatorId: operatorId,
      info: info
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

// http response 拦截器
axios.interceptors.response.use(
  response => {
    if (
      !response.data.status ||
      (response.data.status !== '0' && response.data.status !== 0)
    ) {
      Message.info(response.data.errorMessage)
      // 如果后台认为请求失败
      // 根据状态码进行logout并调整至登录页
      if (response.data.status === '1310' || response.data.status === '1320' || response.data.status === '1330' || response.data.status === 1310 || response.data.status === 1320 || response.data.status === 1330) {
        store.commit(types.LOGOUT)
        router.push({
          path: '/'
        })
      } else if (response.data.status === '1222') {
        return response.data
      } else {

      }
      return Promise.reject(response.data)
    }
    return response.data.data
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          // 401 清除token信息并跳转到登录页面
          Message.warning('登录已超时，请重新登录')
          store.commit(types.LOGOUT)
          router.push({
            path: '/'
          })
          break
        case 404:
          Message.error('您访问的地址不存在，请联系管理员')
          break
        case 500:
          Message.error('系统内部错误，请联系管理员')
          break
        default:
          Message.warning('请求失败')
      }
    }
    // console.log(JSON.stringify(error));
    return Promise.reject(error.response)
  }
)

export default axios
